<template>
    <div class="text-bg-dark">
        <div class="col-6 offset-3">

            <div class="col-4 offset-4 pt-5">
                <img alt="Logo" class="img-fluid" src="./assets/logo.jpeg">
            </div>
            <div class="mb-3">
                <label for="exampleFormControlInput1" class="form-label">Valor: </label>
                <input type="number" class="form-control"  v-model="value" @change="process()">
            </div>
            <div class="col-12">
                <label  class="form-label">Número de parcelas: </label>
                <input type="range" class="form-range" min="1" max="18" v-model="term" @change="process()">
                <h2 class="text-center">{{ term }}</h2>
            </div>

            <div class="form-check form-switch">
                <input class="form-check-input" type="checkbox" role="switch" v-model="limit" @change="process()">
                <label v-if="limit" class="form-check-label">Calcular a partir do limite</label>
                <label v-if="!limit" class="form-check-label">Calcular valor à receber</label>
            </div>
            <div class="jumbotron">
                <h1 class="display-4">Detalhes da simulação</h1>
                <p class="lead">Será cobrado no cartão: {{ this.result.willCharge}}</p>
                <p class="lead">Você receberá: {{ this.result.willReceive }}</p>
                <hr class="my-4">
                <p>Ser{{term > 1? "ão": "á"}} {{term}} parcela{{term > 1? "s": ""}} de {{ result.portion }}</p>
            </div>
            <p class="lead">
                <a class="btn btn-outline-primary btn-lg" role="button" @click="copy">Copiar</a>
            </p>

        </div>
        <br>
        <br>
        <br>
        <br>
        <br>
    </div>
</template>
<script>
export default {
    name: 'App',
    data: function () {
        return {
            rates: {
                receivable: [0, 1.10799, 1.10922, 1.10984, 1.11045, 1.111, 1.11178, 1.1128, 1.11238, 1.11268, 1.11298, 1.11348, 1.11398, 1.117, 1.1175, 1.118, 1.1185, 1.119, 1.120],
                limit: [0, 1.074, 1.0845, 1.0896, 1.0947, 1.0991, 1.1055, 1.1079, 1.1102, 1.1126, 1.1150, 1.1188, 1.1227, 1.1451, 1.1490, 1.153, 1.156, 1.16, 1.1680]
            },
            value: 1000,
            term: 1,
            limit: false,
            result: {
                willCharge: 0,
                willReceive: 0,
                portion: 0
            }
        }
    },
    mounted() {
        this.process()
    },
    methods: {
        process: function(){
            if(this.limit){
                this.result =  {
                    willCharge: this.formatter(this.value),
                    willReceive: this.formatter(this.value /  this.rates.limit[this.term]),
                    portion: this.formatter(this.value  / this.term)
                }
            }
            else{
                this.result =  {
                    willCharge: this.formatter(this.value *  this.rates.receivable[this.term]),
                    willReceive: this.formatter(this.value),
                    portion: this.formatter(this.value *  this.rates.receivable[this.term] / this.term)

                }
            }

        },
        formatter: function(number){
            return number.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })
        },

        copy: function(){
            let  text = `
            Será cobrado no cartão: ${this.result.willCharge}.
            Você receberá: ${ this.result.willReceive }.
            Ser${this.term > 1? "ão": "á"} ${this.term} parcela${this.term > 1? "s": ""} de ${ this.result.portion }`
            navigator.clipboard.writeText(text);
        }

    }
}
</script>
